export class CustomerRoutes {

    constructor(customerService, logger) {
        this._customerService = customerService;
        this._logger = logger;
    }

    get () {
        return [
            (req, res) => {

                return this._customerService.get(req.query)
                    .then(result => {

                        res.send({success : 1, message : "completed", data : result, retry: 0});

                    })
                    .catch(err => {
                        res.send({success : 0, message : " Error! " + err.message, data : {}, retry: 1});
                    });
            }
        ];
    }

    getById () {
        return [
            (req, res) => {

                return this._customerService.getById(req.params.id)
                    .then(result => {

                        res.send({success : 1, message : "completed", data : result, retry: 0});

                    })
                    .catch(err => {
                        res.send({success : 0, message : " Error! " + err.message, data : {}, retry: 1});
                    });
            }
        ];
    }

    getByMerlinAccountNo () {
        return [
            (req, res) => {

                return this._customerService.getByMerlinAccountNo(req.params.id)
                    .then(result => {

                        res.send({success : 1, message : "completed", data : result, retry: 0});

                    })
                    .catch(err => {
                        res.send({success : 0, message : " Error! " + err.message, data : {}, retry: 1});
                    });
            }
        ];
    }
}
